<template>
  <transition name="back-top-top-fade">
    <div class="vue-back-to-top" v-show="visible" @click="backToTop">
      <slot>
        Back To Top
      </slot>
    </div>
  </transition>
</template>
<script>
export default {
  name: 'BackToTop',
  props: {
    visibleOffset: {
      type: [String, Number],
      default: 600
    }
  },
  data () {
    return {
      visible: false
    }
  },
  created () {
    let catchScroll = () => {
      this.visible = (window.pageYOffset > parseInt(this.visibleOffset))
    }

    window.smoothscroll = () => {
      let currentScroll = document.documentElement.scrollTop || document.body.scrollTop
      if (currentScroll > 0) {
        window.requestAnimationFrame(window.smoothscroll)
        window.scrollTo(0, currentScroll - (currentScroll / 5))
      }
    }

    window.onscroll = catchScroll
  },
  methods: {
    backToTop () {
      window.smoothscroll()
    }
  }
}
</script>
<style scoped>
.back-to-top-fade-enter-active, .back-to-top-fade-leave-active {
  transition: opacity .7s;
}
.back-to-top-fade-enter, .back-to-top-fade-leave-to{
  opacity: 0;
}

.vue-back-to-top{
  position: fixed;
  bottom: 40px;
  right: 30px;
  width: 160px;
  color: #ffffff;
  text-align: center;
  line-height: 30px;
  background-color: #f5c85c;
  border-radius: 3px;
  z-index: 1000;
  cursor:pointer;
}

.vue-back-to-top span{
  color:#ffffff;
}
</style>



