<template>
  <div class="particles" v-loading.fullscreen.lock="fullscreenLoading">
    <div id="guide">
      <h1>{{ $t('index.welcome') }}</h1>
      <h2>{{ $t('index.play') }}
        <vue-typer :text="features" :repeat='Infinity' :shuffle='false' initial-action='typing' :pre-type-delay='70' :type-delay='100' :pre-erase-delay='3000' :erase-delay='100' erase-style='backspace' :erase-on-complete='false' caret-animation='smooth'></vue-typer>
      </h2>
      <el-row :gutter="5">
        <el-col :span="18">
          <el-input v-model="user.username" :placeholder="$t('index.enterUsername')" :autofocus="true"></el-input>
        </el-col>
        <el-col :span="6">
          <el-button  @click="start" :disabled="!agreement || user.username.length < 3">{{ $t('index.start') }} !</el-button>
        </el-col>
      </el-row>
      <p class="cursor-hand">
        <i18n path="index.agreement" tag="el-checkbox" v-model="agreement">
          <a place="termsAndConditions" href="#">{{ $t('index.termsAndConditions') }}</a>
        </i18n>
      </p>
      <i18n path="index.accountLogin" tag="p">
        <a place="login" @click="loginFormVisible = true" href="#">{{ $t('index.login') }}</a>
      </i18n>
    </div>
    <vue-particles></vue-particles>
    <el-dialog :visible.sync="loginFormVisible" @close="loginFormClose" size="tiny">
      <span slot="title" class="el-dialog__title">{{ $t('index.signin') }}</span>
      <el-form :model="user" :rules="loginRule" label-width="120px">
        <el-form-item :label="$t('common.username')" prop="username" required>
          <el-input v-model="user.username" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item :label="$t('common.password')" prop="password" required>
          <el-input type="password" v-model="user.password" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="loginFormVisible = false">{{ $t('common.cancel') }}</el-button>
        <el-button type="primary" @click="signin">{{ $t('common.confirm') }}</el-button>
      </div>
    </el-dialog>
    <back-to-top></back-to-top>
    <language-selector v-on:update="changeLanguage" v-bind:currentLanguage="$t('name')"></language-selector>
  </div>
</template>
<script>
import BackToTop from '../../../components/BackToTop'
import LanguageSelector from '../../../components/LanguageSelector'
import { Message } from 'element-ui'

export default {
  name: 'Index',
  components: {
    BackToTop,
    LanguageSelector,
    Message
  },
  data () {
    return {
      features: this.$t('index.features'),
      agreement: true,
      loginFormVisible: false,
      fullscreenLoading: false,
      currentLanguage: localStorage.lang,
      user: {
        username: '',
        password: ''
      },
      loginRule: {
        username: [
          {required: true, trigger: 'blur'},
          {min: 3, trigger: 'blur'}
        ],
        password: [
          {required: true, trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    start () {
      if (this.user.username.length >= 3) {
        this.fullscreenLoading = true
        this.$ajax.post('start', {username: this.user.username}, r => {
          this.fullscreenLoading = false
          this.redirectToDice()
        }, f => {
          this.fullscreenLoading = false
          Message({
            showClose: true,
            message: this.$t('code.' + f.code),
            type: 'error'
          })
        }, e => {
          this.fullscreenLoading = false
          Message({
            showClose: true,
            message: this.$t('common.error'),
            type: 'error'
          })
        })
      }
    },
    changeLanguage (lang) {
      localStorage.lang = lang
      this.$i18n.locale = lang
      this.features = this.$t('index.features')
    },
    loginFormClose () {
      this.user.username = ''
      this.user.password = ''
    },
    signin () {
      this.$ajax.post('signin', this.user, r => {
        this.fullscreenLoading = false
        this.loginFormVisible = false
        this.redirectToDice()
      }, f => {
        this.fullscreenLoading = false
        Message({
          showClose: true,
          message: this.$t('code.' + f.code),
          type: 'error'
        })
      }, e => {
        this.fullscreenLoading = false
        Message({
          showClose: true,
          message: this.$t('common.error'),
          type: 'error'
        })
      })
    },
    redirectToDice () {
      window.location.href = 'http://www.baidu.com'
    }
  }
}
</script>
<style>
.particles{
  position: relative;
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: center;
}
.vue-typer .custom.char.typed{
  color: #dfdfdf;
}
#particles-js {
  position: absolute;
  background-size: cover;
  background: linear-gradient(#030303, #030303, #333333);
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: hidden;
  z-index: 0;
}
#guide {
  color: #dfdfdf;
  text-shadow: 0 0 30px #dfdfdf;
  z-index: 1;
  min-width: 320px;
}
#guide a{
  color: #dfdfdf;
}
#change-language {
  position: absolute;
  right: 10px;
  top: 10px;
  z-index: 9;
}
</style>



