<template>
  <el-popover id="language-selector" placement="left" v-model="visible">
    <template v-for="(value, key) in languages">
      <el-button size="mini" type="text" :key="key" @click="updateLanguage(key)" :disabled="currentLanguage === value">{{ value }}</el-button>
    </template>
    <el-button slot="reference">{{ $t('common.changeLanguage') }}</el-button>
  </el-popover>
</template>
<script>
import Locales from '../locales'

export default {
  props: ['currentLanguage'],
  components: {
    Locales
  },
  data () {
    return {
      visible: false
    }
  },
  computed: {
    languages: function () {
      let langs = {}
      Object.keys(Locales).forEach(function (language) {
        langs[language] = Locales[language].name
      })
      return langs
    }
  },
  methods: {
    updateLanguage (language) {
      this.$emit('update', language)
      this.visible = false
    }
  }
}
</script>
<style scoped>
#language-selector {
  z-index: 9;
  position: absolute;
  bottom: 0;
  right: 0;
}
</style>



